<template>
  <section>
      <img v-bind:src="artikal.slika" alt="">
      <router-link class="nav-link" :to="ruta"><h4>{{artikal.naziv}}</h4></router-link>
      
      
      
      <h3>{{artikal.cena}}</h3>
      <button  v-on:mousedown="smanji()" 
               v-on:mouseup="stopsmanji()"
               >-</button> 
        <input type="text" v-model="kolicina" v-bind:style="sirina" maxlength="3"> 
      <button  v-on:mousedown="povecaj()" 
               v-on:mouseup="stoppovecaj()"
               >+</button> 
      <button v-on:click="dodaj()">Dodaj</button>
  </section>
</template>

<script>
import { basket } from '../main.js'

export default {
  name: 'Artikal',
  props: {
    artikal: Object,
  },
  data(){
    return {
      brojac: 0,
      kolicina: 1,
      ruta: '/proizvod/' + this.artikal.id,
      pozadina : `background-image: url("${this.artikal.slika}")`,

    }
  },
  computed: {
    sirina: function () {
      if(this.kolicina > 99) {
        return "width: 40px";
      }
    }
  },
  methods: {
    smanji: function() {
      this.smanjenje = setInterval(function() {
        if (this.kolicina > 0) this.kolicina--; else this.kolicina = 0;
      }.bind(this),80)
    },
    stopsmanji: function () {
      clearInterval(this.smanjenje);
    },
    povecaj: function() {
      this.povecanje = setInterval(function() {
        this.kolicina++;
      }.bind(this),80)
    },
    stoppovecaj: function () {
      clearInterval(this.povecanje);
    },
    dodaj: function(event) {
      if (this.kolicina > 0) {
        basket.$emit('dodato', {'artikal' : this.artikal, 'kolicina' : this.kolicina});
      } else {
        alert('Kolicina mora biti veca od 0');
      }
    }
  },
  created(){
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  * {
    /* outline: 1px dashed green; */
  }
  section {
   position: relative;
   width: 170px;
   height: 300px;
   padding-top: 10px;
   display: inline-block;
   margin: 20px;
   text-align: center;
   border-radius: 3px;
   vertical-align: top;

   -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.20);
   -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.20);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.20);

    transition: box-shadow 0.4s;
  }
  section:hover {
   -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.50);
   -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.50);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.50);
  }

 .nav-link {
   padding: 2px !important;
  }
  h4 {
    text-align: center;
    margin: 0px;
    font-size: 14px;
    height: 28px;
    overflow: hidden;
    line-height: 15px;
    color: midnightblue;
  }
  input {
    width: 25px;
    text-align: center;
    transition: width 0.4s;
    -webkit-transition: width 0.4;
  }
  img {
    max-width: 100%;
    display: block;
    margin: auto;
    max-height: 170px;
  }
</style>
