<template>
  <body class="row">
    <admin-nav/>
    <div class="col col-sm-9 col-md-9 col-lg-9">
        <table class="table small">
            <thead>
                <tr>
                    <td>#</td>
                    <td>Naziv</td>
                    <td>Cena</td>
                    <td></td>
                    <td></td>
                </tr>
                <tr v-for="(proizvod,index) in proizvodi" v-bind:key="proizvod.id">
                    <td>{{index + 1}}</td>
                    <td>{{proizvod.naziv}}</td>
                    <td>{{proizvod.cena}}</td>
                    <td><button class="btn btn-sm btn-success">izmeni</button></td>
                    <td><button class="btn btn-sm btn-danger" v-on:click="obrisiArtikal(proizvod.id)">izbrisi</button></td>
                </tr>
            </thead>
        </table>
    </div>
     
  </body>
</template>

<script>
import { brisanjeArtikla } from '../main.js'
import AdminNav from '../components/AdminNav'

export default {
  name: 'AdminPocetna',
  props: {
      proizvodi: Array
  },
  components: {
      AdminNav
  },
  methods: {
      obrisiArtikal(id){
          brisanjeArtikla.$emit('brisanjeArtikla', id);
      }
  }
}
</script>

