import AdminUnos from './views/AdminUnos.vue'
import Single from './views/Single.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/category',
      name: 'category',
      component: Category
    },
    {
      path: '/admin',
      name: 'admin',
      component: AdminPocetna
    },
    {
      path: '/unosProizvoda',
      name: 'unos proizvoda',
      component: AdminUnos
    },
    {
      path: '/proizvod/:id',
      name: 'proizvod',
      component: Single
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
