<template>
  <div id="app">
    <app-header v-bind:korpa="korpa"/>

    <router-view v-bind:proizvodi="proizvodi" />
  </div>
</template>


<script>
import { basket } from './main'
import { noviArtikal } from './main'
import { brisanjeArtikla } from './main'
import AppHeader from './components/AppHeader.vue'

export default {
  name: 'app',
  components: {
    AppHeader
  },
  data() {
    return {
      proizvodi: [
        {
          id: 1,
          naziv: "Reebok muške patike Yourflex Train 9.0 CN0762",
          cena: 2490,
          slika: "https://www.eponuda.com/moda_obuca_patike/reebok/1217853_l.jpg",
        }, 
        {
          id: 2,
          naziv: "Adidas muška majica ESS BIGLOGO TEE S98724",
          cena: 1790,
          slika: "https://www.eponuda.com/moda_odeca_majice/adidas/1215786_l.jpg",
        }, 
        {
          id: 3,
          naziv: "Kronos ženska majica LADIES T-SHIRT KRP171284-50 cene",
          cena: 792,
          slika: "https://www.eponuda.com/moda_odeca_majice/kronos/1216116_l.jpg",
        }
      ],
      korpa: [

      ],
      kategorije: [
        {
          naziv: ""
        }
      ]
    }
  },
created() {
  basket.$on('dodato', (data) => {
    this.korpa.push(data);
    console.log(this.data);
    
  })
  noviArtikal.$on('noviArtikal', (data) => {
    data.id = this.proizvodi[this.proizvodi.length - 1].id + 1;
    this.proizvodi.push(data);
  })
  brisanjeArtikla.$on('brisanjeArtikla', (data) => {
    for (let i = 0; i < this.proizvodi.length; i++) {
     if(this.proizvodi[i].id == data) {
       this.proizvodi.splice(i,1);
     }
    }
  })
},
methods: {
  // proslediUKorpu: function (_artikal,_kolicina) {
  //   this.korpa.push({artikal : _artikal, kolicina : _kolicina})
  // }
},
  
}

</script>


