<template>
  
  <header>
    <template v-if="korpaVidljiva">
       <korpa v-bind:sadrzaj="korpa" v-on:zatvori="zatvoriKorpu"></korpa>
    </template>
   
    <nav class="navbar navbar-expand-sm bg-light">

      <!-- Links -->
      <ul class="navbar-nav">
        <li class="nav-item">
          <router-link class="nav-link" to="/">Home</router-link> 
          <!-- <a class="nav-link" href="#">Link 1</a> -->
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/about">About</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/category">Category</router-link>
        </li>
      </ul>

      <ul class="navbar-nav justify-content-end ml-auto">
        <li class="nav-item">
          <router-link class="nav-link" to="/admin">Admin</router-link>
        </li>
        <button v-on:click="korpaVidljiva=true">
          <img src="https://img.icons8.com/dotty/80/000000/shopping-cart.png">
          <span v-bind:class="{ brojac : brojac}">{{ broj }}</span>
        </button>
      </ul>

    </nav>
    <!-- <div id="nav">
      
      
    </div> -->
      
  </header>
</template>

<script>
import korpa from './Korpa'

export default {
  name: 'AppHeader',
  data() {
    return {
      korpaVidljiva: false,
    }
  },
  props: {
    korpa: Array,
  },
  computed: {
    broj() {
      return this.korpa.length;
    },
    brojac() {
      if (this.korpa.length > 0) {
        return true;
      } else return false;
    }

  },
  methods: {
   zatvoriKorpu(){
     this.korpaVidljiva = false;
   }
  },
  components: {
    korpa,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    header {
        width: 100%;
        height: 50px;
        background-color: gray;
    }
    
    button {
      width: 30px;
      height: 30px;
      padding: 0px;
      background-color: rgba(0, 0, 0, 0);
      border: 0px;
      position: relative;
      float: right;
      cursor: pointer;
    }
    button img {
      width: 100%;
    }
    button span {
      position: absolute;
      right: -10px;
      width: 20px;
      height: 20px;
      border-radius: 10px;
      line-height: 20px;
    }
    .brojac {
      background-color: darkgreen;
      color: white;
    }
</style>
